.slider {
  cursor: pointer;
  border: 1px solid var(--accent-color);
  background-color: var(--white-color);
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 16px;
  width: 16px;
  transition: all .4s;
  position: absolute;
  bottom: 1px;
  left: 1px;
}

input:checked + .slider {
  background-color: var(--accent-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--accent-color);
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  background-color: #fff;
  transform: translateX(19px);
}

.slider.round {
  color: var(--accent-color);
  border-radius: 34px;
}

.slider.round:before {
  background-color: var(--accent-color);
  height: 16px;
  width: 16px;
  border-radius: 50%;
}

.switch-light {
  letter-spacing: .02em;
