.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    background-color: rgba(85, 85, 85, 0.454);
    z-index: 10;
    transition: opacity 200ms linear, visibility 200ms linear;
    backdrop-filter: blur(5px);
    
}

.modal-ingrid {
    z-index: 99;
    width: 320px;
    min-height: 673px;
    padding-top: 40px;
    padding-left: 15px;
    padding-right: 25px;

    @include tablet {
        width: 715px;
        min-height: 557px; 
        padding-left: 28px;
        padding-right: 33px;
    }

    @include desktop {
        width: 715px;
        min-height:557px;
    }


    background-color: var(--white-color);

    border-radius: 4px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scaleY(1);
    transition: rtansform 200ms linear;
}

.modal-ingrid__close {
    position: absolute;
    top: 16px;
    right: 16px;
    width: 32px;
    height: 32px;
    @include tablet {
        top: 12px;
        right: 12px;
    }

    fill: var(--black-color);
    background-color: var(--white-color);

    border: none;
    display: flex;
    align-items: center;
    justify-content: center;

    &:hover,
    &:focus {
        fill: var(--accent-color);
        transition-property: fill;
        transition: 250ms;
    }
}

.modal-ingrid__name {
    margin-bottom: 20px;

    @include tablet{
        margin-bottom: 28px;
    }
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 900;
    font-size: 32px;
    line-height: calc(38/32);
}

.modal-ingrid__title{
    margin-bottom: 20px;

    @include tablet {
        margin-bottom: 68px;
        
    }

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: calc(24/16);

    text-transform: uppercase;
}

.line {
    @include tablet {
    position: absolute;
    top: 169px;
    left: 0;
    width: 100%;
    border-bottom: 1px solid var(--accent-color);
    }
}

.modal-ingrid__descrip{

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: calc(28/16);
    color: var(--gray-color);
    margin-bottom: 29px;

    & b{
        color: var(--black-color);

    }
}

.modal-ingrid__list {
    margin-bottom: 32px;

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: calc(28/16);
    color: var(--gray-color);
}

.modal-ingrid__component span {
    color: var(--black-color);
}

.modal-ingrid__add{
    display: flex;
    align-items: center;
    justify-content: start;

    grid-area: boxbutton;

    @include tablet {
        justify-content: end;
    }
}